.header-calendar{
  padding: 10px 10px;
  width: 65%;
  margin: 20px auto;
  // display: flex;
  // justify-content: space-evenly;
  // align-items: center;
  background-color: rgb(109, 151, 241);
  border-radius: 4px;
  box-shadow: 5px 7px 20px rgb(149, 149, 155);
}
.selected-data-show{
  text-align: center;
}
.header .col-sm{
  margin: 5px;
}

.select-calendar{
  width: 75%;
  background-color: rgb(243, 245, 231);
  padding: 7px 20px;
  border: 0.3px solid #000;
}
.btn-light{
  background-color: rgb(243, 245, 231);
  font-weight: bold;
  padding: 7px 20px;
}
.table{
  // padding: 10px;
  margin: 10px auto;
  background-color: rgb(237, 240, 245);
  box-shadow: 5px 7px 9px #ccc;
  border-radius: 4px;
  width: 64%;
  // height: 200px;
  // font-size: 17px;
}
.td-date{
  cursor: pointer;
  &:hover{
    background-color: rgb(243, 245, 237);
  }
}
.today{
  background-color: rgb(78, 75, 224);
  color: #fff;
}
.selected{
  background-color: rgb(243, 245, 237);
  border: 1px solid #000;
  box-shadow: 3px 5px 7px #ccc;
  color: #000;
  font-weight: bold;
}