.navbar{
  // background-color: rgb(62, 114, 173);
  // background-color: rgb(94, 98, 107);
  background-color: rgb(70, 74, 119);
  border-radius: 3px;
  padding: 20px;
}
.navbar__logo{
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #fff;
  border-radius: 8px;
  padding: 7px;
  transition: 0.7s;
  a{
    color: #fff;
    &:hover{
      color: #fff;
    }
  }
  &:hover{
    // background-color: rgb(14, 93, 184);
    background-color: rgb(61, 63, 90);
    box-shadow: 3px 5px 7px rgb(236, 236, 241);
    transition: 0.5s;
  }
}
.menu-links{
  display: flex;
  justify-content: space-evenly;
  align-items: start;
}
.menu-links a{
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
  text-decoration: none;
  display: block;
  padding: 2px;
  transition: 0.7s;
  &:hover{
    background-color: rgb(61, 63, 90);
    border-radius: 3px;
    box-shadow: 5px 7px 11px rgb(236, 236, 241);
    transition: 0.5s;
    color: #fff;
  }
}
.link-time{
  &:hover{
    color: #fff;
  }
}
.navbar__time{
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  border: 1px solid #fff;
  border-radius: 8px;
  padding: 7px;
  transition: 0.7s;
  span{
    font-size: 12px;
  }
  strong{
    font-size: 12px;
    font-weight: bold;
  }
  &:hover{
    background-color: rgb(61, 63, 90);
    box-shadow: 3px 5px 7px rgb(236, 236, 241);
    transition: 0.5s;
    color: #fff;
  }
}
