.carousel{
  max-height: 595px;
  overflow: hidden;
  background-color: rgb(232, 236, 241);
  box-shadow: 5px 7px 11px rgb(181, 188, 202);
  border-radius: 3px;
  padding: 5px;
}
.carousel-item{
  max-height: 570px;
  max-width: 950px;
  margin-left: 100px;
}
.block-info{
  margin-top: 40px;
  font-size: 13px;
}
.info-common{
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  width: 100;
}
.title-info{
  font-weight: bold;
  margin-bottom: 4px;
}
.row_info{
  display: flex;
  justify-content: space-between;
  line-height: 16px;
}
.row_info:nth-child(odd){
  background-color: rgb(224, 219, 219);
}
